@use '@/assets/scss/global.scss' as *;
@use '@/assets/scss/deve.scss' as *;
@use '@/assets/scss/variables.scss' as *;

#panel_aside {
  width: 100%;
  height: 100%;
  position: relative;

  .panel-aside-wrap {
    position: absolute;
    top: 0;
    bottom: 50px;
    overflow-y: scroll;
    width: 100%;
  }

  .panel-aside-bottom {

    position: absolute;
    bottom: 50px;
    height: 50px;
    width: 100%;

    .blog-menu-bar {
      padding-top: 5px;
      display: flex;
      font-size: 12px;
      position: relative;

      >div {
        cursor: pointer;
        margin-top: 6px;
        display: inline-block;
        flex: 1;
        text-align: center;
      }
    }
  }
}
